import React, { useEffect, useState } from "react";
import { Box, VStack, Center } from "@chakra-ui/react";
import testData from "./waferdata_test.json";
import sampleSTDF from "./sampleStdf.json";
import Wafermap from "./components/Wafermap.js";
import FileUpload from "./FileUpload";
import "./App.css";

function App() {
  const [datas, setDatas] = useState([]);
  const [dieInfo, setDieInfo] = useState({ x: 0, y: 0, mouseover: "" });
  var dimensions = { width: 850, height: 850 };

  useEffect(() => {
    if (datas.length === 0) {
      setDatas(sampleSTDF);
    } else {
      setDatas(testData);
    }
  }, []);

  return (
    //add background color : ligth blue in this component
    <div>
      <div style={{ backgroundColor: "#000000cf", display: "flex", justifyContent: "center", marginBottom: "10px" }}>
        <h1 style={{ color: "white" }}>STDF Parsing Tool</h1>
      </div>
      <div style={{ backgroundColor: "lightgray", marginBottom: "10px", border: "solid 3px Black" }}>
        <FileUpload setDatas={setDatas} />
        <br />
        <div className = "info-banner">
          <div className="die-info">X座標 : {dieInfo.x}</div>
          <div className="die-info ">Y座標 : {dieInfo.y}</div>
          <div className="die-info">描述 : {dieInfo.mouseover}</div>
        </div>
      </div>
      <Box style={{ backgroundColor: "whitesmoke", border: "solid 5px black" }}>
        <Center>
          <VStack>
            <Wafermap points={datas} configuration={dimensions} setDieInfo={setDieInfo} />
          </VStack>
        </Center>
      </Box>
    </div>
  );
}

export default App;
