import React, { useRef, useEffect } from 'react'
import PropTypes from 'prop-types'
import * as d3 from 'd3'

const Wafermap = ({ points, configuration, setDieInfo }) => {
  const svgRef = useRef(null)

  useEffect(() => {
    const max_x = d3.max(points, p => p.x)
    const max_y = d3.max(points, p => p.y)
    const centerX = max_x / 2;
    const centerY = max_y / 2;

    const rate = max_x > max_y ? (max_x / max_y) : (max_y / max_x)

    const radius = max_x + 2
    const vWidth = 4 * max_x + 1
    const vHeight = 4 * max_y + 1
    const svg = d3.select(svgRef.current)

    // svg.append('circle')
    //   .style('fill', 'white')
    //   .style('stroke', 'white')
    //   .style('stroke-width', 0.025)
    //   .attr('id', 'd3-outer-circ')
    //   .attr('cx', centerX)
    //   .attr('cy', centerY)

    // svg.append('circle')
    //   .style('fill', 'white')
    //   .style('stroke', 'white')
    //   .style('stroke-width', 0.025)
    //   .attr('id', 'd3-inner-circ')
    //   .attr('cx', centerX)
    //   .attr('cy', centerY)

    svg.append('g').attr('id', 'd3-die')

    svg.attr('viewBox', [centerX - vWidth / 2, centerY - vHeight / 2, vWidth, vHeight])

    
    /**
     * Represents the selection of SVG elements for the wafer map.
     */
    const g = svg.select('#d3-die')
      .selectAll('rect')
      .data(points)
      .join('rect')
      .attr('x', point => point.x * 1.5)
      .attr('y',point => point.y * (rate + 1))
      .attr('width',  1)
      .attr('height', 1* rate)
      .attr('fill', p => p.color)
      .attr('stroke', 'black')
      .attr('stroke-width', 0.025)
      .on('mouseover', mouseover)
      .on('mouseout', mouseout)

    const outer_c = svg.select('#d3-outer-circ')
      .attr('r', radius + 0.2)

    const inner_c = svg.select('#d3-inner-circ')
      .attr('r', radius)

    function mouseover(d, i) {
      setDieInfo(i);
    }

    var mouseout = function (d, i) {  // eslint-disable-line no-unused-vars

    }

    svg.call(d3.zoom()
      .extent([[centerX, centerY], [vWidth, vHeight]])
      .scaleExtent([1, 8])
      .on('zoom', zoomed))

    function zoomed({ transform }) {
      g.attr('transform', transform)
      outer_c.attr('transform', transform)
      inner_c.attr('transform', transform)
    }
  }, [points])

  return <svg ref={svgRef}
    width={configuration.width}
    height={configuration.height} />
}

Wafermap.propTypes = {
  points: PropTypes.array.isRequired,
  configuration: PropTypes.object.isRequired
}

export default Wafermap
